.main{
    width: 100%;
    min-height: 100vh;
    padding: 20px 40px;
    background: white;
    

}

.h1{
    text-align: center;
    font-size:  1.2rem;
    font-weight: 300;
    text-transform: uppercase;

}

.h1{
    text-align: center;
    font-size:  2.3rem;
    font-weight: 700;
    text-transform: uppercase;

    
}
.tab{
    display: flex;
    justify-content: center;
    gap:20px;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px 0px;
}

.tab .timer , .tab .square{
    min-width: 120px;
    height: 120px;
    background: #111;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    
}
.square{
    position: relative;
    top: 500px;
    left: -80px;
}
.tab .square{
    border-radius: 3rem;

}
.timer{
    position: relative;
    left: 16%
}
.tab p{
    font-size: 3rem;
    font-weight: 700;
    line-height: 1;
}
.content{
    user-select: none;
    overflow: hidden;
}

.char{
    text-align: center;
    font-size: 2rem;
    color:#9a9a9a;
    padding:0 1px;
    margin: 5px 2px 5px 0;
    border-radius: 2px;


}
.char.active{
    color: skyblue;
    border-radius: 0;
    border-bottom: 3px solid skyblue;
    animation: cursorBlink 2s 1s infinite;

}

@keyframes cursorBlink{
    0%{border-color : #3295db;}
    50%{border-color: transparent}
    100%{border-color : #3295db;}

}

.char.correct {
    color: #95c590;
    background: #edf7e7;
}

.char.incorrect {
    color: red;
    background: #ffdcd9;

}
.restart{
    display: block;
    width: 100%;
    text-align: center;
    margin: 10px auto;
    font-size: 2rem;
    font-weight: 900;
    color:#9a9a9a;
    cursor:pointer;
    user-select: none;
}

input{
    opacity: 0;
}

